.game-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 10000;
}

.game-modal-content {
  background: #333;
  padding: 20px;
  border-radius: 8px;
  width: 90%;
  height: 90%;
  max-width: 1200px; /* Adjust as needed */
  max-height: 800px; /* Adjust as needed */
  position: relative;
  display: flex;
  flex-direction: column;
}

.game-modal-close {
  position: absolute;
  top: 10px;
  right: 10px;
  background: none;
  border: none;
  font-size: 24px;
  color: #fff;
  cursor: pointer;
  z-index: 10001;
}
